import PublishSubscribe from 'publish-subscribe-js'
import { getWithdrawNotify } from '@/api/modules/config.js'
import { isNativeClient, lodash } from '@/utils/helper'
import DialogUi from './ui.js'

export default function (vm) {
  const { base = {}, dialog = {}, source = {} } = vm.$store.state
  const { DUALOG_MANAGER_KEY = 'dualog_manager_key', DUALOG_MANAGER_IS_NEXT = 'dualog_manager_is_next' } = dialog

  // 弹窗关闭
  const callback = (res = 'cancel') => {
    PublishSubscribe.publish(DUALOG_MANAGER_IS_NEXT, false)
    if (res == 'cancel') PublishSubscribe.publish(DUALOG_MANAGER_KEY)
  }
  // 弹窗弹出
  const next = (key = '', params = {}) => {
    if (!key) return callback('cancel')

    PublishSubscribe.publish(DUALOG_MANAGER_IS_NEXT, true)
    DialogUi[key](vm, params, callback)
  }

  return {
    // 版本更新弹窗
    version: function (params = {}) {
      // nativeVer 当前壳包版本. 不需要与当前版本判断。commmonConfig 传入了当前版本，服务端已经处理好了。
      // is_up:是否开启更新 2:开启 0:不开启
      // is_force:是否强制更新 1:强制更新 0：不强制
      // android_download_url: apk下载链接
      const { systemConfig = {}, nativeVer = null } = base
      const { is_up = 0, is_force = 0, android_download_url = '' } = systemConfig
      // 不开启更新 || 不是壳包
      if (is_up != 2 || !isNativeClient()) return callback('cancel')

      next('version', { ...params, is_force, android_download_url })
    },
    // 公告弹窗
    aviso: function (params = {}) {
      const { notices = [] } = base
      if (!notices || notices.length == 0) return callback('cancel')

      next('aviso', { ...params, notices })
    },
    // 轮播公告弹窗
    notices: function (params = {}) {
      const { carouselList = [] } = base
      if (!carouselList || carouselList.length == 0) return callback('cancel')

      next('notices', { ...params, bannerArr: carouselList })
    },
    // vip 是否弹提示可领取弹窗
    vipReward: function (params = {}) {
      const { user = {}, showRedDot = {} } = base
      const date = vm.$u.useTimestamp(new Date(), 'yyyy-MM-dd') // 日期
      const is_reward = localStorage.getItem(`IS_REWARD_TIME_${user.uid}`)

      // vip有红点 && 当天没有弹过 && 不在保级状态(is_keep_level 0保级中 1未保级) && 不在vip页面
      if (
        showRedDot?.vip_cash_gift?.is_show &&
        is_reward != date &&
        !!user.is_keep_level &&
        vm.$route.name != 'recompensas'
      ) {
        next('vipReward', { ...params, date })
      } else {
        callback('cancel')
      }
    },
    // 洗码待领取弹窗
    betReward: function (params = {}) {
      const { user = {}, showRedDot = {}, systemConfig = {} } = base
      // 储存在本地的金额档位
      const is_clean_bet = localStorage.getItem(`IS_CLEAN_BET_${user.uid}`)
      // 是否自动领取洗码金额 auto_clean_bet_switch 0:否，1:是
      // 洗码金额达到档位出现弹窗 bet_pop_up_config
      const { auto_clean_bet_switch = 0, bet_pop_up_config = '' } = systemConfig
      const bet_pop_up_config_arr = bet_pop_up_config.split(',')
      // 红点信息，如果没有登录就不会有红点信息，所以就不要判断是否登录了
      const { clean_bet = {} } = showRedDot
      const clean_bet_value = clean_bet.value || 0
      // 计算出，当前金额在哪个档位
      let now_clean = null
      bet_pop_up_config_arr.forEach((bet, i) => {
        if (clean_bet_value >= bet) {
          if (bet_pop_up_config_arr[i + 1] && bet_pop_up_config_arr[i + 1] > clean_bet_value) {
            now_clean = i + 1
          } else {
            now_clean = bet_pop_up_config_arr.length
          }
        }
      })

      // 不自动领取 && 有档位 && 未弹窗过 && 不在洗码页面
      if (auto_clean_bet_switch != 1 && now_clean && is_clean_bet != now_clean && vm.$route.name != 'washCode') {
        next('betReward', { ...params, now_clean })
      } else {
        callback('cancel')
      }
    },
    // 全民代待领取弹窗
    referReward: function (params = {}) {
      const { user = {}, showRedDot = {} } = base
      const date = vm.$u.useTimestamp(new Date(), 'yyyy-MM-dd') // 日期
      const is_reward = localStorage.getItem(`IS_REFER_REWARD_TIME_${user.uid}`)

      // vip有红点 && 当天没有弹过 && 不在全民代页面
      if (showRedDot?.user_spread_way?.is_show && is_reward != date && vm.$route.name != 'recomendar') {
        next('referReward', { ...params, date })
      } else {
        callback('cancel')
      }
    },
    // vip降级的弹窗
    relegation: function (params = {}) {
      const { user = {} } = base
      const { uid = 0, is_keep_level = 0, keep_level_conf = 0 } = user
      // 储存在本地时间
      const date = vm.$u.useTimestamp(new Date(), 'yyyy-MM-dd')
      const is_relegation = localStorage.getItem(`IS_RELEGATION_TIME_${uid}`)

      /**
       * vip 保级的弹窗
       * 洗码弹窗结束 && 当天还没有弹窗 && vip处于保级状态中(is_keep_level 0保级中 1未保级) && 充值金额存在 && 不在vip页面
       */
      if (is_relegation != date && is_keep_level != 1 && !!keep_level_conf && vm.$route.name != 'recompensas') {
        let content = vm.$u.replaceSrt(
          vm.$t('UI_Relegation_Text'),
          '{vip}',
          vm.$store.getters['base/getVipCurrentLevel']()
        )
        content = vm.$u.replaceSrt(content, '{money}', String(keep_level_conf))

        next('relegation', { ...params, content, date })
      } else {
        callback('cancel')
      }
    },
    // vip升级弹窗
    perform: function (params = {}) {
      next('perform', { ...params })
    },
    // 充值成功弹窗
    deposit: function (params = {}) {
      next('deposit', { ...params })
    },
    // 体育维护弹窗
    mainten: async function (params = {}) {
      next('mainten', { ...params })
    },
    // 体育提现提醒弹窗
    remind: async function (params = {}) {
      const { user = {}, languageType = '' } = base
      const res = await getWithdrawNotify({ token: user.token, language: languageType }).catch(e => e)
      if (!res || res.code != 0) return callback('cancel')

      const resData = res.data && Object.prototype.toString.call(res.data) == '[object Object]' ? res.data : {}
      // status:1 开启弹窗 0 关闭 tip: 内容
      const { status = 0, tip = '' } = resData
      if (status != 1) return callback('cancel')

      next('remind', { ...params, content: tip })
    },
    /**
     * 幸运转盘弹窗
     * 规则
     *  在活动时间内
     *      有次数玩家，每次登陆(刷新)时进行弹窗
     *      无次数玩家，每日弹一次
     *  超过活动时间时
     *      有次数玩家，每次登陆(刷新)时进行弹窗
     *      无次数玩家不会进行弹窗
     */
    lucky: function (params = {}) {
      const { systemConfig = {}, showRedDot = {}, user = {} } = base
      const { lucky_wheel_activity_conf = {} } = systemConfig
      const { start_time = 0, end_time = 0 } = lucky_wheel_activity_conf
      const current_time = parseInt(new Date().getTime() / 1000)
      const redDot = showRedDot['red_dot_wheel_draw_num'] || {}
      // is_show 次数
      const { is_show = 0, value = 0 } = redDot
      // 是否在活动时间内
      const isActivity = current_time > start_time && current_time < end_time
      // 今日的时间
      const dateToday = vm.$u.useTimestamp(new Date(), 'yyyy-MM-dd')

      let luckyLocal = localStorage.getItem('CC_LUCKY_DIALOG') || '{}'
      luckyLocal = JSON.parse(luckyLocal)
      const lucky_dialog = luckyLocal[`${dateToday}_${user.uid}`] || 0
      const isLucky = is_show > 0 ? true : isActivity ? (lucky_dialog ? false : true) : false
      if (isLucky) {
        next('lucky', { ...params, is_show, isActivity, dateToday })
      } else {
        callback('cancel')
      }
    },
    /**
     * 生涯充值弹窗
     * register_account_send_gold: 邮箱绑定 ==> { reward: 绑定奖励, total_recharge: 需生涯充值, is_window: 绑定弹窗（1开 0关） }
     * register_tel_send_gold: 手机绑定 ======> { reward: 绑定奖励, total_recharge: 需生涯充值, is_window: 绑定弹窗（1开 0关） }
     * tel: 绑定的手机号
     * account: 绑定的邮箱
     * total_recharge: 用户总的充值金额
     */
    career: function (params = {}) {
      // 游客不弹出
      if (vm.$store.getters['base/isTourist']) return callback('cancel')

      const { systemConfig = {}, user = {} } = base
      const { register_account_send_gold = {}, register_tel_send_gold = {} } = systemConfig
      const { account = '', tel = '', total_recharge: user_recharge = '0' } = user

      // 配置信息
      let config = {}
      if (!account) config = register_account_send_gold
      if (!tel) config = register_tel_send_gold

      const { is_window = 0, total_recharge: config_recharge = 0, reward = 0 } = config
      if (is_window != 1) return callback('cancel')
      if (Number(user_recharge) < Number(config_recharge)) return callback('cancel')

      next('career', { ...params, gold: reward })
    },
    // 回归任务弹窗
    regression: function (params = {}) {
      // 未开启不弹出
      if (!vm.$store.getters['base/isTaskShow']) return callback('cancel')

      next('regression', { ...params })
    },
    // 州信息填写弹窗
    cpfStateInfo: function (params = {}) {
      const { systemConfig = {}, user = {} } = base
      const { state_select_popup_switch = 0, state_select_popup_switch_open_time = 0 } = systemConfig
      const { user_state_name = '', reg_time = 0 } = user
      // 是否新用户
      const isNewUser = Number(reg_time) > state_select_popup_switch_open_time
      // 开关打开 && 没有填写州信息 && 并且已登录 && 新用户
      if (state_select_popup_switch && !user_state_name && !vm.$store.getters['base/isTourist'] && isNewUser)
        return next('cpfStateInfo', { ...params })

      return callback('cancel')
    },
    // vip签到奖励
    vipSign: function (params = {}) {
      const {
        vipSignInfo: { list = [] }
      } = base
      const isReceive = list.some(item => item.sign == 0)
      const { show = false, popup = false } = vm.$store.getters['base/isVipSignShow'] || {}

      // 首次是否弹出 ==> 开关开启 && 配置了首次弹出 && 不是游客 && 未领取
      if (!(show && popup && isReceive && !vm.$store.getters['base/isTourist'])) return callback('cancel')
      next('vipSign', { ...params })
    },
    // cpf被占用弹窗
    cpfOccupiedConfirm: function (params = {}) {
      const { user = {} } = base
      const { reg_cpf_have_been_bind_popup = 0 } = user

      if (reg_cpf_have_been_bind_popup == 1) return next('cpfOccupiedConfirm', { ...params })
      callback('cancel')
    },
    // 二充
    secondRecharge: function (params = {}) {
      const { user = {}, systemConfig = {} } = base
      const { secondRechargeList = [] } = source
      let isSecondRecharge = false
      const itemFind = secondRechargeList.find(v => v.uid == user.uid)

      if (itemFind) isSecondRecharge = itemFind.isSecondRecharge

      if (
        systemConfig?.second_recharge_ext_gift_switch == 1 &&
        lodash.isObject(user?.secord_recharge_ext_gift) &&
        user?.secord_recharge_ext_gift?.finish_state != 1 &&
        !isSecondRecharge
      ) {
        const { start_time = 0, end_time = 0 } = user?.secord_recharge_ext_gift
        const currentTime = parseInt(new Date().getTime() / 1000)
        if (start_time >= currentTime || end_time <= currentTime) return callback('cancel')

        secondRechargeList.push({ isSecondRecharge: true, uid: user.uid })
        vm.$store.commit('source/SET_SECOND_RECHARGE_LIST', secondRechargeList)

        return next('secondRecharge', { ...params })
      }
      return callback('cancel')
    },
    // 邀新活动转盘弹窗
    inviteGift: function (params = {}) {
      const { activityInvite } = source
      const { activity_user_task = null, activity_info = {} } = activityInvite || {}
      const { status = 0 } = activity_info
      //1.有任务不弹 || 2.status为0不弹
      if (activity_user_task || !status) return callback('cancel')
      // activity_user_task == null  非任务，首次弹转盘
      next('inviteGift', { ...params })
    },
    // 邀新活动记录弹窗
    inviteQuest: function (params = {}) {
      const { activityInvite = {} } = source
      const { activity_user_task = null, activity_info = {} } = activityInvite
      const { status = 0 } = activity_info

      if (status != 1 && !activity_user_task) return callback('cancel')
      // activity_user_task == null  非任务，首次弹转盘
      if (activity_user_task) {
        next('inviteQuest', { ...params })
      } else {
        return callback('cancel')
      }
    }
  }
}
