<template>
  <game-box id="app_box" :key="updateAppKey">
    <keep-alive include="menuIndex">
      <router-view />
    </keep-alive>
  </game-box>
</template>

<script>
import NoSleep from '@/utils/nosleep.js'
import GameBox from '@/components/game-container/game-box'
import _style from '@style/_global/_color.module.scss'
import { getPlatform } from '@/utils/helper'

export default {
  components: {
    GameBox
  },
  computed: {
    // 更新项目
    updateAppKey() {
      return this.$store.getters['common/getUpdateAppKey']
    },
    // 路由name
    routerName() {
      const { name = '' } = this.$route
      return name
    }
  },
  created() {
    const metaThemeColor = document.querySelector('meta[name="theme-color"]')
    try {
      if (metaThemeColor) metaThemeColor.setAttribute('content', _style.top_nav_bg)
    } catch (error) {
      // console.log(error);
    }
  },
  mounted() {
    if (getPlatform() === 'ios' || getPlatform() === 'android') {
      this.createNoSleep()
    }
  },
  methods: {
    // 防止息屏限制，保持屏幕常亮
    createNoSleep() {
      try {
        const noSleep = new NoSleep()
        document.addEventListener('click', () => {
          noSleep.enable()
        })
      } catch (error) {
        console.log('no NoSleep')
      }
    }
  }
}
</script>
<style lang="scss">
@import '@style/components/game-container.modules.scss';

body {
  background: $project_bg;
}

#app {
  width: 100%;
  height: 100%;
  font-family: $font-family-base;
}

.fixed {
  position: fixed;
  left: 0;
  top: 0;
}

::-webkit-scrollbar {
  display: none;
  /* Chrome Safari */
}
</style>
