const webm =
  'data:video/webm;base64,'
const mp4 =
  'data:video/mp4;base64,'

function oldBroswer() {
  if (typeof navigator === 'undefined') return false

  // 检查 iOS 版本
  const userAgent = navigator.userAgent
  const iosMatch = /OS (\d+)[_](\d+)/.exec(userAgent)
  let isOldIOS = false
  if (iosMatch) {
    const iosVersion = parseFloat(`${iosMatch[1]}.${iosMatch[2]}`)
    isOldIOS = iosVersion < 10 // 检查 iOS 版本是否小于 10
  }
  // 检查 Chrome 版本
  const chromeMatch = /Chrome\/(\d+)/.exec(userAgent)
  let isOldChrome = false
  if (chromeMatch) {
    const chromeVersion = parseInt(chromeMatch[1], 10)
    isOldChrome = chromeVersion < 85 // 检查 Chrome 版本是否小于 85
  }
  return isOldIOS || isOldChrome // 返回是否是旧 iOS 或旧 Chrome
}
function nativeWakeLock() {
  return 'wakeLock' in navigator
}
// export default
export default class NoSleep {
  constructor() {
    this.enabled = false
    if (nativeWakeLock()) {
      this._wakeLock = null
      const handleVisibilityChange = () => {
        if (this._wakeLock !== null && document.visibilityState === 'visible') {
          this.enable()
        }
      }
      // 监听是否属于可视区，当前标签或者浏览器是否属于激活状态
      document.addEventListener('visibilitychange', handleVisibilityChange)
      document.addEventListener('fullscreenchange', handleVisibilityChange)
    } else if (oldBroswer()) {
      this.noSleepTimer &&
        this.noSleepTimer.length > 0 &&
        this.noSleepTimer.forEach(timer => {
          clearInterval(timer)
        })
      this.noSleepTimer = []
    } else {
      this.noSleepVideo = document.createElement('video')
      this.noSleepVideo.setAttribute('playsinline', '')
      //Compatible with older browsers, requires the use of webm,mp4 two video formats
      this._addSourceToVideo(this.noSleepVideo, 'webm', webm)
      this._addSourceToVideo(this.noSleepVideo, 'mp4', mp4)

      this.noSleepVideo.addEventListener('loadedmetadata', () => {
        if (this.noSleepVideo.duration <= 1) {
          this.noSleepVideo.setAttribute('loop', '')
        } else {
          this.noSleepVideo.addEventListener('timeupdate', () => {
            if (this.noSleepVideo.currentTime > 0.5) {
              this.noSleepVideo.currentTime = Math.random()
            }
          })
        }
      })
    }
  }

  _addSourceToVideo(element, type, dataURI) {
    var source = document.createElement('source')
    source.src = dataURI
    source.type = `video/${type}`
    element.appendChild(source)
  }

  get isEnabled() {
    return this.enabled
  }

  enable() {
    if (document.visibilityState !== 'visible') return
    if (nativeWakeLock()) {
      return navigator.wakeLock
        .request('screen')
        .then(wakeLock => {
          this._wakeLock = wakeLock
          this.enabled = true
          console.warn('Wake Lock active.')
          this._wakeLock.addEventListener('release', () => {
            console.warn('Wake Lock released.')
            // when not user allow to disable, delayed 5000ms to restart
            if (this.enabled) {
              setTimeout(() => {
                this.enable()
              }, 5000)
            }
          })
        })
        .catch(err => {
          this.enabled = false
          console.error(`${err.name},${err.message}`)
          throw err
        })
    } else if (oldBroswer()) {
      this.disable()
      let t = window.setInterval(() => {
        if (!document.hidden) {
          window.location.href = window.location.href.split('#')[0]
          window.setTimeout(window.stop, 0)
        }
      }, 15000)
      this.noSleepTimer.push(t)
      this.enabled = true
      return Promise.resolve()
    } else {
      return this.noSleepVideo
        .play()
        .then(res => {
          this.enabled = true
          return res
        })
        .catch(err => {
          this.enabled = false
          throw err
        })
    }
  }

  disable() {
    if (nativeWakeLock()) {
      if (this._wakeLock) {
        this._wakeLock.release()
      }
      this._wakeLock = null
    } else if (oldBroswer()) {
      if (this.noSleepTimer) {
        console.warn(`NoSleep now disabled for older browsers.`)
        this.noSleepTimer &&
          this.noSleepTimer.length > 0 &&
          this.noSleepTimer.forEach(timer => {
            clearInterval(timer)
          })
        this.noSleepTimer = []
      }
    } else {
      this.noSleepVideo.pause()
    }
    this.enabled = false
  }
}
