import { jump } from '@/utils/linkMap'
import { sport_btr_gid } from '@/api/setting.js'

export default {
  version: function (vm, params = {}, callback) {
    const { is_force = 0, android_download_url = '' } = params

    vm.$pop
      .version({
        modeBool: !!is_force,
        content: vm.$t('UI_Version_Content'),
        cancel: () => {
          vm.$pop.version().hide(() => callback('cancel'))
        },
        submit: () => {
          if (!is_force) vm.$pop.version().hide()
          if (android_download_url) window.open(android_download_url)
          callback('ok')
        }
      })
      .show()
  },
  aviso: function (vm, params = {}, callback) {
    const { notices = [] } = params

    vm.$pop
      .aviso({
        notices,
        onCancel: () => {
          callback('cancel')
        },
        onLink: item => {
          jump(item, vm)
          callback('ok')
        }
      })
      .show()
  },
  notices: function (vm, params = {}, callback) {
    const { bannerArr = [] } = params

    vm.$pop
      .notices({
        bannerArr,
        onCancel: () => {
          callback('cancel')
        },
        onLink: item => {
          jump(item, vm)
          callback('ok')
        }
      })
      .show()
  },
  vipReward: function (vm, params = {}, callback) {
    const { date = '' } = params
    const { user = {} } = vm.$store?.state?.base

    vm.$pop
      .vipReward({
        content: vm.$t('UI_Reward_Vip_Text'),
        primaryText: vm.$t('UI_Recommendar.Receber'),
        cancelText: vm.$t('UI_Get_Later'),
        onSubmit: () => {
          localStorage.setItem(`IS_REWARD_TIME_${user.uid}`, date)
          vm.$router.push({ name: 'recompensas' })
          callback('ok')
        },
        onCancel: () => {
          localStorage.setItem(`IS_REWARD_TIME_${user.uid}`, date)
          callback('cancel')
        }
      })
      .show()
  },
  betReward: function (vm, params = {}, callback) {
    const { now_clean = 0 } = params
    const { user = {} } = vm.$store?.state?.base

    vm.$pop
      .betReward({
        content: vm.$t('UI_Reward_Clean_Text'),
        primaryText: vm.$t('UI_Recommendar.Receber'),
        cancelText: vm.$t('UI_Get_Later'),
        onSubmit: () => {
          localStorage.setItem(`IS_CLEAN_BET_${user.uid}`, now_clean)
          vm.$router.push({ name: 'washCode' })

          callback('ok')
        },
        onCancel: () => {
          localStorage.setItem(`IS_CLEAN_BET_${user.uid}`, now_clean)

          callback('cancel')
        }
      })
      .show()
  },
  referReward: function (vm, params = {}, callback) {
    const { date = '' } = params
    const { user = {} } = vm.$store?.state?.base

    vm.$pop
      .referReward({
        content: vm.$t('UI_Refer_reward'),
        primaryText: vm.$t('UI_Recommendar.Receber'),
        cancelText: vm.$t('UI_Get_Later'),
        onSubmit: () => {
          localStorage.setItem(`IS_REFER_REWARD_TIME_${user.uid}`, date)
          vm.$router.push({ name: 'recomendar' })

          callback('ok')
        },
        onCancel: () => {
          localStorage.setItem(`IS_REFER_REWARD_TIME_${user.uid}`, date)

          callback('cancel')
        }
      })
      .show()
  },
  relegation: function (vm, params = {}, callback) {
    const { date = '', content = '' } = params
    const { user = {} } = vm.$store?.state?.base

    vm.$pop
      .relegation({
        title: vm.$t('General.General_9'),
        content,
        moneyText: vm.$t('UI_Relegation_Money'),
        banusText: vm.$t('UI_Relegation_Banus'),
        submitText: vm.$t('UI_VIP_Recarega_Btn'),
        onSubmit: () => {
          localStorage.setItem(`IS_RELEGATION_TIME_${user.uid}`, date)
          vm.$store.dispatch('modal/setDepositWithdrawShow')

          callback('ok')
        },
        onCancel: () => {
          localStorage.setItem(`IS_RELEGATION_TIME_${user.uid}`, date)

          callback('cancel')
        }
      })
      .show()
  },
  perform: function (vm, params = {}, callback) {
    vm.$pop
      .perform({
        ...params,
        onSubmit: () => {
          vm.$store.dispatch('modal/setDepositWithdrawShow', { show: false })

          vm.$router.push({ name: 'recompensas' })
          callback('ok')
        },
        onCancel: () => {
          callback('cancel')
        }
      })
      .show()
  },
  deposit: function (vm, params = {}, callback) {
    vm.$pop
      .deposit({
        ...params,
        onCancel: async () => {
          callback('cancel')

          // 判断是否在体育赛事页面，上分
          if (vm.$route.name == 'sportView') {
            const { user = {}, languageType = '', isApp = false } = vm.$store?.state?.base
            const params = {
              token: user.token,
              gid: sport_btr_gid,
              language: languageType,
              type: isApp ? 100 : 101,
              direction: 1
            }

            vm.$store.commit('source/SET_IS_ROTATE', true)
            await vm.$store.dispatch('sport/upperDivision', params).catch(e => e)
            vm.$store.commit('source/SET_IS_ROTATE', false)
          }
          // 如果是特殊提现入口，则关闭
          const { withdrawEntrance = 0 } = vm.$store?.state?.modal
          if (withdrawEntrance == 1 || withdrawEntrance == 2) {
            vm.$store.dispatch('modal/setDepositWithdrawShow', { show: false })
          }
        }
      })
      .show()
  },
  mainten: async function (vm, params = {}, callback) {
    vm.$pop
      .mainten({
        ...params,
        success: () => {
          // callback('ok');
          callback('cancel')
        },
        error: () => {
          callback('cancel')
        }
      })
      .show()
  },
  remind: async function (vm, params = {}, callback) {
    vm.$pop
      .remind({
        ...params,
        onSubmit: () => {
          vm.$store.dispatch('modal/setDepositWithdrawShow', { tab: 'withdraw' })
          callback('ok')
        },
        onCancel: () => {
          callback('cancel')
        }
      })
      .show()
  },
  lucky: function (vm, params = {}, callback) {
    const { is_show = 0, isActivity = false, dateToday = '' } = params
    const { user = {} } = vm.$store?.state?.base

    vm.$pop
      .lucky({
        cancel: () => {
          if (!Number(is_show) && isActivity) {
            let luckyLocal = localStorage.getItem('CC_LUCKY_DIALOG') || '{}'
            luckyLocal = JSON.parse(luckyLocal)
            luckyLocal[`${dateToday}_${user.uid}`] = '1'
            localStorage.setItem('CC_LUCKY_DIALOG', JSON.stringify(luckyLocal))
          }

          callback('cancel')
        }
      })
      .show()
  },
  career: function (vm, params = {}, callback) {
    vm.$pop
      .career({
        ...params,
        cancel: () => {
          callback('cancel')
        },
        submit: () => {
          callback('cancel')
        }
      })
      .show()
  },
  regression: function (vm, params = {}, callback) {
    vm.$pop
      .regression({
        ...params,
        onSubmit: () => {
          callback('ok')
        },
        onCancel: () => {
          callback('cancel')
        }
      })
      .show()
  },
  cpfStateInfo: function (vm, params = {}, callback) {
    vm.$pop
      .cpfStateInfo({
        ...params,
        onCancel: () => {
          callback('cancel')
        },
        onSubmit: () => {
          callback('ok')
        }
      })
      .show()
  },
  vipSign: function (vm, params = {}, callback) {
    vm.$pop
      .vipSign({
        ...params,
        onCancel: () => {
          callback('cancel')
        }
      })
      .show()
  },
  cpfOccupiedConfirm: function (vm, params = {}, callback) {
    vm.$pop
      .confirm({
        ...params,
        title: vm.$t('General.General_13'),
        confirmText: vm.$t('UI_Contact_Service'),
        content: vm.$t('UI_CPF_Occupied'),
        success: () => {
          vm.$pop
            .kefu({
              onClose: () => {
                callback('cancel')
              }
            })
            .show()
        },
        error: () => {
          callback('cancel')
        }
      })
      .show()
  },
  secondRecharge: function (vm, params = {}, callback) {
    vm.$pop
      .secondRecharge({
        onCancel: () => {
          callback('cancel')
        },
        onSubmit: () => {
          callback('ok')
        }
      })
      .show()
  },
  inviteGift: function (vm, params = {}, callback) {
    vm.$pop
      .inviteGift({
        onCancel: () => {
          callback('cancel')
        },
        onSubmit: () => {
          callback('ok')
        }
      })
      .show()
  },
  inviteQuest: function (vm, params = {}, callback) {
    vm.$pop
      .inviteQuest({
        onCancel: () => {
          callback('cancel')
        },
        onSubmit: () => {
          callback('ok')
        }
      })
      .show()
  }
}
