import PublishSubscribe from 'publish-subscribe-js'
import initList from './initList.js'
import control from './control.js'

/**
 * 弹窗管理-观察者
 * 用于接收弹窗通知
 * 派发弹窗显示通知
 * @param {*} vm vue实例
 * @param {*} overallList 弹窗队列列表
 * @param {*} isNext 是否正在执行弹窗
 * ***
 * @param {*} init 初始化 Function
 * @param {*} clear 清除序列 Function
 * @param {*} handleNotice 通知处理 Function
 * @param {*} joinList 加入队列 Function
 */
export default {
  $app: null,
  overallList: [],
  isNext: false,
  init(vm) {
    this.$app = vm

    const {
      DUALOG_MANAGER_KEY = 'dualog_manager_key',
      DUALOG_MANAGER_IS_NEXT = 'dualog_manager_is_next',
      DUALOG_MANAGER_CLEAR = 'dualog_manager_clear'
    } = vm.$store?.state?.dialog || {}

    PublishSubscribe.subscribe(DUALOG_MANAGER_KEY, data => this.handleNotice(data))
    PublishSubscribe.subscribe(DUALOG_MANAGER_IS_NEXT, (bool = false) => (this.isNext = bool))
    PublishSubscribe.subscribe(DUALOG_MANAGER_CLEAR, () => this.clear())
  },
  clear() {
    this.isNext = false
    this.overallList = []
    // 让弹窗都监听到，控制弹窗消失
    const { popupState = true } = this.$app.$store.state.dialog || {}
    this.$app.$store.commit('dialog/SET_POPUP_STATE', !popupState)
  },
  /**
   * @param {string} data 路由name，当前模块执行
   * @param {object} data 加入队列数据
   * @param {null|undefined} data 默认执行
   */
  handleNotice(data) {
    const { base = {}, source = {} } = this.$app.$store.state || {}

    // 加入队列
    if (typeof data == 'object' && Object.keys(data).length > 0) this.joinList(data)
    // 根据模块，更新弹窗队列列表
    if (typeof data == 'string') {
      // 添加回归任务弹窗
      const { dialogInfo = {} } = base
      const { backWindowList = [] } = dialogInfo
      const backList = backWindowList.map(actionItem => {
        return { sign: 'regression', params: { actionItem } }
      })

      const key = data == 'menu' ? data : 'common'
      this.overallList = [...initList[key], ...backList]
    }
    // 是否正在游戏中
    const { isToGame = false } = source
    if (isToGame) return
    // 是否正在执行，防止多次执行
    if (this.isNext) return
    // 是否有弹窗
    if (this.overallList.length == 0) return

    // ======== 执行弹窗
    const [firstItem, ...restList] = this.overallList
    const { sign = '', params = {} } = firstItem
    this.overallList.shift()

    control(this.$app)[sign](params)
  },
  /**
   * @param {*} key 队列 的 key【是否存在，存在就是插入弹窗，不存在就是默认执行】
   * @param {*} seat first:插入序列第一个，不传就是插入末尾
   * @param {*} params 加入队列时候的参数
   */
  joinList({ key = '', seat = '', params = {} }) {
    // 判断队列里有这个弹窗嘛
    // const index = this.overallList.findIndex(f => f.sign == key);
    // if (index != -1) return;

    const item = { sign: key, params }
    if (seat == 'first') {
      this.overallList.unshift(item)
    } else {
      this.overallList.push(item)
    }
  }
}
